*{
  margin: 0;
  padding: 0;
}
.app-input{
   text-align: center;
    background-color: rgb(240, 173, 147);
    height: 300px;
    color: chocolate;
    font-size: 18px;
    
}
input{
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid orangered;
  outline: none;
  padding: 10px 5px;
  width: 200px;
  margin-top: 50px;
}
.app-input button {
  background-color: rgb(207, 65, 65);
  color: white;
  padding: 10px 20px;
  border-radius: 30px;
  margin-top: 10px;
}
.navActive{
  color: rgb(226, 24, 226)
}
nav{
  background-color: plum;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  margin-top: 1px;
}
nav ul{
  display: flex;
  flex-direction: column;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 60%;
  color: white;
  font-weight: bold;
 
}
nav ul li{
  cursor: pointer;
  font-size: 14px;
  transition: all 0.5s ease-in-out;
  padding-top: 7px;
}
nav ul li:hover{
  transform: scale(1.2);
}
.app-container{
  margin-left: 610px;
  margin-top: 10px;
  color: blueviolet; 
}